import React from 'react'
import ReactDOM from 'react-dom'

import "./index.scss"

class Baner extends React.Component {
    constructor(props) {
        super(props)
        this.state = { width: window.innerWidth, height: window.innerHeight }
    }

    render() {
        return (
          <div className="wp-block-group alignfull tw-mb-0 tw-mt-0 bns-frontend" style={{backgroundColor: "#ffffff"}}>
              <div class="wp-block-group__inner-container">
                  <figure className="wp-block-image alignfull size-full tw-mb-0 tw-mt-0 bns">
                      <img src={(this.state.width > 425) ? this.props.desktop : this.props.mobile} alt="baner spotkania" />
                  </figure>
              </div>
          </div>
        )
    }
    

    updateDimensions = () => {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateDimensions)
    }
    
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions)
    }
    
    
    
    
    
}

const divsToUpdate = document.querySelectorAll(".bns-update-me")

divsToUpdate.forEach(function(div) {
    const data = JSON.parse(div.querySelector("pre").innerHTML)
    ReactDOM.render(<Baner {...data} />, div)
    div.classList.remove(".bns-update-me")
})

